import React from "react";
import styled from "styled-components";

import DisplayImage from "../../Images/Jpg/Slider.jpg";
import ScrollImage from "../../Images/Jpg/bgimage.png";
import newsImage from "../../Images/Jpg/WhatsApp Image 2020-11-29 at 4.14.28 PM.jpeg";
import { useState } from "react";
import { useEffect } from "react";
import Fade from "react-reveal/Fade";

const ImageHolder = styled.div`
  position: relative;
`;

const IMAGE = styled.img`
  width: 100%;
  border-radius: 10%;
  box-shadow: 12px 12px 16px 0 rgba(149, 122, 122, 0.25),
    -12px -12px 12px 0 rgba(238, 230, 230, 0.3);
  height: 250px;

  @media (max-width: 1100px) {
    height: 200px;
  }
`;

function Scroll() {
  const [scroll, setScroll] = useState(1);

  useEffect(() => {
    setTimeout(() => {
      if (scroll >= 3) {
        return setScroll(1);
      }
      setScroll(1 + scroll);
    }, 5000);
  });

  const renderImage = () => {
    if (scroll === 1) {
      return <IMAGE src={DisplayImage} alt="" width="100%" />;
    }
    if (scroll === 2) {
      return (
          <IMAGE src={ScrollImage} alt="" width="100%" />
      );
    } else {
      return (
          <IMAGE src={newsImage} alt="" width="100%" />
      );
    }
  };

  return (
    <ImageHolder>
      {console.log(scroll)} <Fade top> {renderImage()}</Fade>
    </ImageHolder>
  );
}

export default Scroll;
