import React from "react";
import "./Navbar.css";
import Burger from "./Burger";
import styled from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEnvelopeOpen
} from "@fortawesome/free-solid-svg-icons";
import Logo from "../../Images/logo/logo_vueron/vueron18black1.png"


const PhoneNumber = styled.div`
  a {
    text-decoration: none;
    padding: 5px 2px;
    align-items: center;
    background: linear-gradient(
      -45deg,
      rgba(0, 0, 0, 0.22),
      rgba(255, 255, 255, 0.25)
    );
    box-shadow: 2px 2px 6px 0 rgba(149, 122, 122, 0.25),
      -1px -1px 2px 0 rgba(238, 230, 230, 0.3);
    border-radius: 50px;
    display: flex;
    background: #ffffff;
    justify-content: center;
    width: 150px;
    color: black;
    margin:0 20px;
  }
  @media (max-width:1000px) {
    display:none;
  }
`;

class Navbar extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      prevScrollpos: window.pageYOffset,
      visible: true,
    };
  }

  // Adds an event listener when the component is mount.
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  // Remove the event listener when the component is unmount.
  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }



  // Hide or show the menu.
  handleScroll = () => {
    const { prevScrollpos } = this.state;

    const currentScrollPos = window.pageYOffset;
    const visible = prevScrollpos > currentScrollPos;

    this.setState({
      prevScrollpos: currentScrollPos,
      visible,
    });
  };
  render() {
    let transparent;
    if (this.state.prevScrollpos < 2) {
      transparent = "background_transparent";
    } else {
      
    }
    return (
      <div className={`Navbar_Container ${transparent}`}>
          <div className="Navbar_logo">
            <a href="/" style={{display:"flex",flexDirection:"column",alignItems:"center",justifyContent:"center"}} ><img src={Logo} alt=""/><span style={{fontSize:"8px",fontWeight:"bold",fontFamily:"monospace",textTransform:"uppercase"}}>Unreal technology for real people</span></a>
          </div>
          <div className="Navbar_Link">
          </div>
          <Burger/>
          <PhoneNumber>
          <a href="mailto:info@vueron.ai">
            <FontAwesomeIcon icon={faEnvelopeOpen} />
            &nbsp; info@vueron.ai
          </a>
        </PhoneNumber>
      </div>
    );
  }
}



export default Navbar;